
import {defineComponent} from "vue";

let timer: any = null;

export default defineComponent({
  data() {
    return {
      step:  1,
      startFlag: false,
      voteFlag: null
    }
  },
  methods: {
    getImgUrl(pic: string) {
      return require('./assets/'+pic)
    },
    next(): void {
      if (this.startFlag == false) return;

      if (timer !== null) {
        clearInterval(timer)
      }
      if (this.step ===10) {
        alert("테스트를 완료하였습니다 설문조사를 해주세요.");
        const el = (document.getElementById("content") as HTMLElement);
        this.$smoothScroll({
          scrollTo: el,
        })
        return;
      }
      this.step = this.step + 1;
      if (this.step === 4 || this.step === 6) {
        timer = setTimeout(() => {
          this.next();
        }, 1000)
      }
    },
    checkActive(index: number) {
      let step = this.step;
      if (step == 4) {
        step = 3;
      } else if (step == 5 || step == 6) {
        step = 4;
      } else if (step >= 7) {
        step = this.step -2
      }
      if (index === step) {
        return true;
      }
      return false;
    },
    vote() {
      alert("참여해 주셔서 감사합니다.")
    },
    start() {
      this.startFlag = true;
      alert("튜토리얼을 시작합니다. 화면을 계속 클릭하면 다음으로 넘어갑니다")
    }
  }
})
